.login-form input {
  width: 70%;
  margin: auto;
  font-size: 28px;
  border: none;
  margin-bottom: 15px;
  text-align: center;
  border: 2px solid #2FAEC5;
  border-radius: 10px;
}

.login-form button {
 background-color: #2FAEC5;
  border: none;
  color: white;
  padding: 15px 80px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  font-size: 20px;
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
  width: 70%;
  margin: auto;
}

.signup-form {
    width: 90%;
